
import { getUserSession } from '@/app/actions';
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from '@/components/ui/card';
import { Button } from '@/components/ui/button';
import Link from 'next/link';
import { PlusSquare, ListChecks } from 'lucide-react';

export const dynamic = 'force-dynamic';

export default async function DashboardPage() {
  const user = await getUserSession(); // User is already checked in layout, but good for direct data

  if (!user) {
    // This should ideally not be reached if layout handles redirect
    return <p>请先登录。</p>;
  }

  return (
    <div className="space-y-6">
      <Card className="shadow-lg">
        <CardHeader>
          <CardTitle className="text-3xl font-bold text-primary">欢迎回来，{user.nickname}！</CardTitle>
          <CardDescription>这里是您的个人中心，您可以在此管理您的建筑作品。</CardDescription>
        </CardHeader>
        <CardContent className="space-y-4">
          <p className="text-muted-foreground">
            使用左侧的导航菜单开始您的创作之旅，或管理已提交的作品。
          </p>
          <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
            <Link href="/dashboard/submit" passHref>
              <Button variant="outline" className="w-full h-20 text-lg">
                <PlusSquare className="mr-3 h-6 w-6" />
                提交新作品
              </Button>
            </Link>
            <Link href="/dashboard/my-projects" passHref>
              <Button variant="outline" className="w-full h-20 text-lg">
                <ListChecks className="mr-3 h-6 w-6" />
                查看我的作品
              </Button>
            </Link>
          </div>
        </CardContent>
      </Card>
      
      {/* Placeholder for future content like "My Votes" or quick stats */}
      {/* 
      <Card>
        <CardHeader>
          <CardTitle>快速统计</CardTitle>
        </CardHeader>
        <CardContent>
          <p>您已提交 X 个作品。</p>
          <p>您已为 Y 个作品投票。</p>
        </CardContent>
      </Card>
      */}
    </div>
  );
}
